import React from 'react';
import { NextPage } from 'next';
import ProjectMarkdown from '../../src/ProjectMarkdown';
import { getMarkdown } from '../../util/projectData';

type Props = {
  markdown: string
}

const ProjectPage: NextPage<Props> = (props) => {
  console.log("rendering [pid]");
  return (<ProjectMarkdown projectMD={props.markdown} />)
}

ProjectPage.getInitialProps = async ({ query }) => {
  console.log("GetInitialProps for [pid].tsx with query: ", query);
  const markdown = getMarkdown(query.pid as string);
  markdown.then(markdown => {})
  return {markdown};
}

export default ProjectPage;